import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { ToastController } from 'ionic-angular';
import { Order } from '../../models/order';
import { ConfigProvider } from '../config/config';
import { BehaviorSubject } from "rxjs/BehaviorSubject";

@Injectable()
export class OrderProvider {

  private orderState = new BehaviorSubject({
    isSubmitting: false
  });

  constructor(private http: Http, private toastController: ToastController, private config: ConfigProvider) {}

  post(order: Order){

    this.orderState.next({
      isSubmitting: true
    });

    this.http.post(this.config.baseApiOrderUrl + this.config.environment + this.config.orderEndpoint, order, {})
    .toPromise().then(result => {

      this.orderState.next({
        isSubmitting: false
      });

      order.clear();

      this.toastController.create({
        message: this.config.orderSuccessMessage,
        duration: this.config.orderSuccessMessageDuration
      }).present();
    }).catch(error =>{
      console.error(error);
      this.orderState.next({
        isSubmitting: true
      });

      //todo
    });
  }

  isSubmitting(){
    return this.orderState.map(orderState => orderState.isSubmitting);
  }
}



