import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ConfigProvider {

  feeAmount: number = 7;
  public mainPhoneNumber: string = '425-835-1666';
  public baseApiOrderUrl: string = 'https://2vv8rth4gd.execute-api.us-east-2.amazonaws.com';
  public environment: string =  '/prod';
  public stateEndpoint: string = '/state';
  public orderEndpoint: string = '/order';
  public contactUsEndpoint: string = '/contact-us';
  public orderSuccessMessage: string = 'Thanks for your order! A runner will send you a text shortly and coordinate your delivery.';
  public orderSuccessMessageDuration: number = 10000;
  public contactUsSuccessMessage: string = 'Message sent!';
  public contactUsSuccessMessageDuration: number = 5000;

  constructor(public http: Http) {}
}



