import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { ToastController } from 'ionic-angular';
import { Message } from '../../models/message';
import { ConfigProvider } from '../config/config';
import { BehaviorSubject } from "rxjs/BehaviorSubject";

@Injectable()
export class ContactProvider {

  private messageState = new BehaviorSubject({
    isSubmitting: false
  });

  constructor(private http: Http, private toastController: ToastController, private config: ConfigProvider) {}

  post(message: Message){

    this.messageState.next({
      isSubmitting: true
    });

    this.http.post(this.config.baseApiOrderUrl + this.config.environment + this.config.contactUsEndpoint, message, {})
    .toPromise().then(result => {

      this.messageState.next({
        isSubmitting: false
      });

      message.clear();

      this.toastController.create({
        message: this.config.contactUsSuccessMessage,
        duration: this.config.contactUsSuccessMessageDuration
      }).present();
    }).catch(error =>{
      console.error(error);
      this.messageState.next({
        isSubmitting: true
      });

      //todo
    });
  }

  isSubmitting(){
    return this.messageState.map(messageState => messageState.isSubmitting);
  }
}



