import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { OrderPage } from '../pages/order/order';
import { HowItWorksPage } from '../pages/how-it-works/how-it-works';
import { FrequentlyAskedQuestionsPage } from '../pages/frequently-asked-questions/frequently-asked-questions';
import { ContactUsPage } from '../pages/contact-us/contact-us';
import { TermsOfServicePage } from '../pages/terms-of-service/terms-of-service';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = OrderPage;

  pages: Array<{title: string, component: any}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      // { title: 'Home', component: HomePage },
      // { title: 'List', component: ListPage },
      { title: 'Place An Order', component: OrderPage },
      { title: 'How It Works', component: HowItWorksPage },
      { title: 'Frequently Asked Questions', component: FrequentlyAskedQuestionsPage },
      { title: 'Contact Us', component: ContactUsPage },
      { title: 'Terms', component: TermsOfServicePage }
    ];
  }

  initializeApp() {
    if((this.platform.is('core') || this.platform.is('mobileweb'))){
      this.splashScreen.hide();
    }

    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    if((this.platform.is('core') || this.platform.is('mobileweb'))){
      this.nav.push(page.component.name);// this seems to be a hack, using to update the url
    }

    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



